
/* header */

.header {
  box-shadow: 1px 1px 1px 0 rgba(0,0,0,.1);
  width: 100%;
  z-index: 3;
  top: 0;
  margin: 0 0 2em 0;
}

.header ul {
  margin: 0;
  padding: 0 0 60px 0;
  list-style: none;
  overflow: hidden;
}

.header li a {
  display: block;
  padding: 20px 0;
  text-decoration: none;
}


.header .logo {
  display: block;
  float: left;
  font-size: 2em;
  padding: 10px 20px 0 56px;
  text-decoration: none;
}

/* menu */

.header .menu {
  clear: both;
  max-height: 0;
  transition: max-height .4s ease-out;
  padding: 0 40px 0 0;
}

/* menu icon */

.header .menu-icon-darkMode {
  cursor: pointer;
  display: inline-block;
  float: left;
  padding: 28px 20px;
  position: relative;
  user-select: none;
}

.header .menu-icon-darkMode .navicon {
  display: block;
  height: 1px;
  position: relative;
  transition: background .2s ease-out;
  width: 22px;
}

.header .menu-icon-darkMode .navicon:before,
.header .menu-icon-darkMode .navicon:after {
  background: #eee;
  content: '';
  display: block;
  height: 100%;
  position: absolute;
  transition: all .2s ease-out;
  width: 100%;
}

.header .menu-icon-darkMode .navicon:before {
  top: 5px;
}

.header .menu-icon-darkMode .navicon:after {
  top: -5px;
}

/* menu btn */

.header .menu-btn {
  display: none;
}

.header .menu-btn:checked ~ .menu {
  max-height: 240px;
}

.header .menu-btn:checked ~ .menu-icon-darkMode .navicon {
  background: transparent;
}

.header .menu-btn:checked ~ .menu-icon-darkMode .navicon:before {
  transform: rotate(-45deg);
}

.header .menu-btn:checked ~ .menu-icon-darkMode .navicon:after {
  transform: rotate(45deg);
}

.header .menu-btn:checked ~ .menu-icon-darkMode:not(.steps) .navicon:before,
.header .menu-btn:checked ~ .menu-icon-darkMode:not(.steps) .navicon:after {
  top: 0;
}

/* 48em = 768px */

@media (min-width: 48em) {
  .header li {
    float: left;
  }
  .header .menu {
    clear: none;
    float: left;
    max-height: none;
  }
  .header .menu-icon-darkMode {
    display: none;
  }
}


.header .menu-icon-lightMode {
  cursor: pointer;
  display: inline-block;
  float: left;
  padding: 28px 20px;
  position: relative;
  user-select: none;
}

.header .menu-icon-lightMode .navicon {
  display: block;
  height: 2px;
  position: relative;
  transition: background .2s ease-out;
  width: 22px;
}

.header .menu-icon-lightMode .navicon:before,
.header .menu-icon-lightMode .navicon:after {
  background: #111;
  content: '';
  display: block;
  height: 100%;
  position: absolute;
  transition: all .2s ease-out;
  width: 100%;
}

.header .menu-icon-lightMode .navicon:before {
  top: 5px;
}

.header .menu-icon-lightMode .navicon:after {
  top: -5px;
}

/* menu btn */

.header .menu-btn {
  display: none;
}

.header .menu-btn:checked ~ .menu {
  max-height: 240px;
}

.header .menu-btn:checked ~ .menu-icon-lightMode .navicon {
  background: transparent;
}

.header .menu-btn:checked ~ .menu-icon-lightMode .navicon:before {
  transform: rotate(-45deg);
}

.header .menu-btn:checked ~ .menu-icon-lightMode .navicon:after {
  transform: rotate(45deg);
}

.header .menu-btn:checked ~ .menu-icon-lightMode:not(.steps) .navicon:before,
.header .menu-btn:checked ~ .menu-icon-lightMode:not(.steps) .navicon:after {
  top: 0;
}

/* 48em = 768px */

@media (min-width: 48em) {
  .header li {
    float: left;
  }
  .header .menu {
    clear: none;
    float: left;
    max-height: none;
  }
  .header .menu-icon-lightMode {
    display: none;
  }
}
